      outline: 0;
      box-shadow: 0 0 0 0.2rem rgba(51, 102, 255, 0.25);
    }
  
    &::placeholder {
      color: $gray-400;
      opacity: 1;
    }
  
    &:disabled, &[readonly] {
      background-color: $gray-200;
      opacity: 1;
    }
  }
  
