        justify-content: center;
        width: auto;
        padding-left: 0;
      }
  
      .form-check-input {
        position: relative;
        flex-shrink: 0;
        margin-top: 0;
        margin-right: 0.25rem;
        margin-left: 0;
      }
  
      .custom-control {
        align-items: center;
        justify-content: center;
      }
  
      .custom-control-label {
        margin-bottom: 0;
      }
    }
  }
  
  .fade {
    transition: opacity 0.15s linear;
  
    &:not(.show) {
      opacity: 0;
    }
  }
  
  @media (prefers-reduced-motion: reduce) {
    .fade {
      transition: none;
    }
  }
  
  .collapse:not(.show) {
    display: none;
  }
  
  .collapsing {
    position: relative;
    height: 0;
    overflow: hidden;
    transition: height 0.35s ease;
  }
  
  @media (prefers-reduced-motion: reduce) {
    .collapsing {
      transition: none;
    }
  }
  
