/* ######  Error  ###### */

.main-signin-wrapper {
    padding: 20px;
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .main-card-signin, .main-card-signup {
    padding: 20px;
    display: flex;
    justify-content: space-between;
    background-color: $white;
    position: relative;
    box-shadow: $shadow;
  }
  
  @media (max-width: 767px) {
    .main-card-signin {
      display: block !important;
      padding: 1.5rem;
    }
  
    .page-signin-style:before {
      display: none;
    }
  
    .main-contacts-list {
      height: 500px;
      position: relative;
    }
  
    .main-nav-line-chat {
      padding: 20px !important;
    }
  
    .main-content-body-show .card {
      border: 0 !important;
    }
  }
  
  @media (min-width: 480px) {
    .main-card-signin {
      border: 1px solid #e3e3e3;
      padding: 0;
      max-width: 750px;
  
      &.construction {
        max-width: 1000px;
      }
    }
  
    .main-card-signup {
      border: 1px solid #e3e3e3;
      padding: 0;
      max-width: 850px;
    }
  
    .sign-up-body {
      padding: 2.6rem !important;
    }
  }
  
  .main-signin-header {
    h2 {
      font-weight: 500;
      color: $primary;
      letter-spacing: -1px;
    }
  
    h4 {
      font-weight: 500;
      color: #14112d;
      margin-bottom: 25px;
      font-size: 16px;
    }
  
    label {
      color: #a5a0b1;
    }
  
    .form-control {
      color: #14112d;
      font-weight: 500;
      border-width: 2px;
      border-color: #e3e3e3;
  
      &:focus {
        border-color: $gray-400;
        box-shadow: none;
      }
  
      &::placeholder {
        font-weight: 400;
        color: #a5a0b1;
      }
    }
  
    .btn, .sp-container button {
      margin-top: 25px;
    }
  }
  
  .sp-container .main-signin-header button {
    margin-top: 25px;
  }
  
  .main-signin-footer {
    p {
      color: #a5a0b1;
  
      &:first-child {
        margin-bottom: 5px;
      }
  
      &:last-child {
        margin-bottom: 0;
      }
    }
  
    a {
      color: #14112d;
      font-weight: 700;
  
      &:hover, &:focus {
        color: $primary;
      }
    }
  }
  
  .page-signin-style {
    width: 100%;
    height: 100%;
    background: url(../img/media/4.jpg);
    left: 0;
    top: 0;
    bottom: 0;
    position: relative;
  
    &:before {
      content: '';
      background: rgba(40, 92, 247, 0.8);
      position: absolute;
      height: 100%;
      width: 100%;
      z-index: 1;
      left: 0;
      top: 0;
    }
  }
  
  .authentication-pages {
    position: relative;
    z-index: 99;
  }
  
  @media (min-width: 992px) {
    .page-signin-style {
      height: auto !important;
    }
  }
  
  /* ###### 7.10 Signup  ###### */
  
  .main-signup-wrapper {
    flex: 1;
    display: flex;
    justify-content: center;
    background-color: rgba(244, 245, 248, 0.2);
  }
  
  @media (min-width: 992px) {
    .main-signup-wrapper {
      justify-content: flex-end;
    }
  }
  
  .main-column-signup-left {
    flex: 1;
    padding: 30px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    display: none;
  
    > div {
      max-width: 500px;
    }
  
    .typcn {
      font-size: 80px;
      margin-bottom: 50px;
  
      &::before {
        width: auto;
      }
    }
  
    .main-logo {
      font-size: 40px;
      line-height: 1;
    }
  
    h5 {
      color: #14112d;
      font-weight: 500;
      font-size: 18px;
      margin-bottom: 25px;
    }
  
    p {
      line-height: 1.7;
      margin-bottom: 25px;
    }
  
    .btn, .sp-container button {
      border-width: 2px;
      padding-left: 25px;
      padding-right: 25px;
      font-weight: 700;
      text-transform: uppercase;
      height: 38px;
    }
  }
  
  @media (min-width: 576px) {
    .main-column-signup-left {
      padding: 40px;
    }
  }
  
  @media (min-width: 992px) {
    .main-column-signup-left {
      display: flex;
    }
  }
  
  .sp-container .main-column-signup-left button {
    border-width: 2px;
    padding-left: 25px;
    padding-right: 25px;
    font-weight: 700;
    text-transform: uppercase;
    height: 38px;
  }
  
  .main-column-signup {
    background-color: $white;
    padding: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
  @media (min-width: 576px) {
    .main-column-signup {
      padding: 40px;
      width: 500px;
    }
  }
  
  @media (min-width: 992px) {
    .main-column-signup {
      border-left: 1px solid #e3e3e3;
    }
  }
  
  .main-signup-header {
    h2 {
      font-weight: 500;
      color: $primary;
      letter-spacing: -1px;
    }
  
    h4 {
      font-size: 20px;
      font-weight: 400;
      color: #14112d;
      margin-bottom: 25px;
    }
  
    label {
      color: #a5a0b1;
    }
  
    .form-control {
      color: #14112d;
      font-weight: 500;
      border-width: 2px;
      border-color: #e3e3e3;
  
      &:focus {
        border-color: $gray-400;
        box-shadow: none;
      }
  
      &::placeholder {
        font-weight: 400;
        color: #a5a0b1;
      }
    }
  
    > .btn {
      margin-top: 25px;
    }
  }
  
  .sp-container .main-signup-header > button {
    margin-top: 25px;
  }
  
  .main-signup-header .row {
    margin-top: 20px;
  
    > div {
      .btn i, .sp-container button i {
        font-size: 15px;
        line-height: 0;
        margin-right: 5px;
      }
    }
  }
  
  .sp-container .main-signup-header .row > div button i {
    font-size: 15px;
    line-height: 0;
    margin-right: 5px;
  }
  
  .main-signup-header .row > div:first-child {
    .btn, .sp-container button {
      background-color: #4267b2;
      color: $white;
    }
  }
  
  .sp-container .main-signup-header .row > div:first-child button {
    background-color: #4267b2;
    color: $white;
  }
  
  .main-signup-header .row > div:first-child {
    .btn:hover, .sp-container button:hover {
      background-color: #375694;
      color: $white;
    }
  }
  
  .sp-container .main-signup-header .row > div:first-child button:hover {
    background-color: #375694;
    color: $white;
  }
  
  .main-signup-header .row > div:first-child {
    .btn:focus, .sp-container button:focus {
      background-color: #375694;
      color: $white;
    }
  }
  
  .sp-container .main-signup-header .row > div:first-child button:focus {
    background-color: #375694;
    color: $white;
  }
  
  .main-signup-footer {
    p {
      color: #a5a0b1;
      margin-bottom: 0;
    }
  
    a {
      color: #14112d;
      font-weight: 700;
  
      &:hover, &:focus {
        color: $primary;
      }
    }
  }
  
  /* ###### 7.11 Error  ###### */
  
  .main-error-wrapper {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 20px;
    text-align: center;
  
    h1 {
      line-height: 1;
      font-size: 150px;
      font-weight: 700;
      color: $gray-900;
    }
  
    h2 {
      font-weight: 500;
      color: $dark;
      letter-spacing: -.5px;
      margin-bottom: 15px;
    }
  
    h6 {
      margin-bottom: 40px;
      font-size: 14px;
    }
  
    .btn, .sp-container button {
      border-width: 2px;
      font-weight: 700;
    }
  }
  
  .sp-container .main-error-wrapper button {
    border-width: 2px;
    font-weight: 700;
  }
  
  /* ######  Error  ###### */