/* ###### Calendar  ###### */

@media (min-width: 992px) {
    .main-content-calendar {
      padding: 25px 0;
    }
  }
  
  .main-content-calendar {
    .container, .container-fluid {
      padding: 20px 0 0;
    }
  }
  
  @media (min-width: 992px) {
    .main-content-calendar {
      .container, .container-fluid {
        padding-top: 0;
      }
    }
  }
  
  @media (min-width: 576px) {
    .main-content-left-calendar {
      width: 300px;
    }
  }
  
  @media (min-width: 992px) {
    .main-content-left-calendar {
      width: 230px;
    }
  }
  
  .main-datepicker {
    .ui-datepicker-inline {
      margin-top: 0;
    }
  
    .ui-datepicker {
      .ui-datepicker-title {
        color: $primary;
        letter-spacing: -.2px;
      }
  
      .ui-datepicker-calendar {
        width: 100%;
  
        th {
          padding: 5px;
          font-size: 10px;
          color: $gray-900;
          text-align: center;
        }
  
        td {
          text-align: center;
        }
  
        th.ui-datepicker-week-end:first-child:not(.ui-datepicker-other-month) a {
          color: $danger;
        }
  
        td {
          &.ui-datepicker-week-end:first-child:not(.ui-datepicker-other-month) a {
            color: $danger;
          }
  
          padding: 0 1px;
  
          span, a {
            padding: 4px 5px;
            font-family: Verdana, sans-serif;
            font-size: 11px;
            color: $gray-700;
          }
        }
  
        .ui-datepicker-today a {
          background-color: $primary;
          color: $white;
          border-radius: 50%;
        }
      }
    }
  }
  
  .main-nav-calendar-event .nav-link {
    font-size: 13px;
    position: relative;
  
    i {
      font-size: 21px;
      position: relative;
      margin-left: 10px;
      top: -.5px;
    }
  
    &.exclude {
      color: $gray-600;
  
      i {
        color: $gray-600;
      }
  
      &::before {
        content: '';
        position: absolute;
        top: 50%;
        left: 0;
        right: 0;
        border-top: 1px solid $gray-600;
      }
    }
  }
  
  @media (min-width: 992px) {
    .main-content-body-calendar {
      padding-left: 25px;
    }
  }
  
  .main-calendar {
    border-color: $gray-300;
  
    .fc-content, .fc-divider, .fc-list-heading td, .fc-list-view, .fc-popover, .fc-row, tbody, td, th, thead {
      border-color: $gray-300;
    }
  
    .fc-header-toolbar {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 15px;
      padding: 0 20px;
  
      h2 {
        margin-bottom: 0;
        color: $gray-900;
        font-size: 20px;
        font-weight: 700;
      }
  
      .fc-right {
        order: 3;
      }
  
      .fc-clear {
        display: none;
      }
  
      button {
        height: 38px;
        background-color: $white;
        background-image: none;
        border: 1px solid $gray-300;
        box-shadow: none;
        padding: 0 15px;
        display: flex;
        align-items: center;
        color: $gray-900;
        font-size: 13px;
        text-transform: capitalize;
        outline: none;
        text-shadow: none;
        border-radius: 0;
  
        &:hover, &:focus {
          background-color: $gray-200;
        }
  
        &.fc-prev-button, &.fc-next-button {
          padding: 0;
          width: 38px;
          justify-content: center;
        }
  
        &.fc-prev-button span, &.fc-next-button span {
          margin: 0;
        }
  
        &.fc-state-active {
          background-color: $gray-200;
        }
  
        &.fc-today-button {
          color: $white;
          background-color: $primary;
          border-color: #1a53ff;
  
          &.fc-state-disabled {
            border-color: $gray-300;
            background-color: $gray-100;
            color: $gray-500;
            cursor: default;
          }
        }
      }
    }
  }
  
  @media (min-width: 992px) {
    .main-calendar .fc-header-toolbar {
      padding: 0;
      margin-bottom: 24px;
    }
  }
  
  @media (min-width: 576px) {
    .main-calendar .fc-header-toolbar h2 {
      font-size: 22px;
    }
  }
  
  @media (max-width: 767.98px) {
    .main-calendar .fc-header-toolbar {
      .fc-center {
        order: 1;
        flex: 0 0 100%;
        display: flex;
        justify-content: center;
        margin-bottom: 20px;
      }
  
      .fc-left {
        order: 2;
        flex: 0 0 50%;
      }
  
      .fc-right {
        flex: 0 0 50%;
        display: flex;
        justify-content: flex-end;
      }
    }
  }
  
  .img-thumbnail p {
    font-size: 13px;
  }
  
  @media (max-width: 575px) {
    .main-calendar .fc-header-toolbar button {
      &.fc-prev-button, &.fc-next-button {
        width: 32px;
        height: 32px;
      }
  
      &.fc-today-button {
        height: 32px;
        padding-left: 10px;
        padding-right: 10px;
        font-size: 11px;
      }
  
      &.fc-month-button, &.fc-agendaWeek-button, &.fc-agendaDay-button, &.fc-listWeek-button, &.fc-listMonth-button {
        text-indent: -9999px;
        width: 32px;
        height: 32px;
        position: relative;
        color: transparent;
      }
  
      &.fc-month-button::before, &.fc-agendaWeek-button::before, &.fc-agendaDay-button::before, &.fc-listWeek-button::before, &.fc-listMonth-button::before {
        position: absolute;
        top: 6px;
        left: 10px;
        z-index: 100;
        display: inline-block;
        text-indent: 0;
        font-size: 12px;
        font-weight: 700;
        color: $default-color;
      }
  
      &.fc-month-button::before {
        content: 'M';
      }
  
      &.fc-agendaWeek-button::before {
        content: 'W';
      }
  
      &.fc-agendaDay-button::before {
        content: 'D';
      }
  
      &.fc-listWeek-button::before, &.fc-listMonth-button::before {
        content: 'L';
        left: 11px;
      }
    }
  }
  
  .main-calendar {
    .fc-icon-left-single-arrow, .fc-icon-right-single-arrow {
      top: 0;
    }
  
    .fc-icon-left-single-arrow::after, .fc-icon-right-single-arrow::after {
      font-family: 'Ionicons';
      font-size: 18px;
      font-weight: 400;
      line-height: 1;
    }
  
    .fc-icon-left-single-arrow::after {
      content: '\f3cf';
    }
  
    .fc-icon-right-single-arrow::after {
      content: '\f3d1';
    }
  
    .fc-head-container .fc-day-header {
      font-size: 11px;
      font-weight: 700;
      text-transform: uppercase;
      padding: 5px 0;
      color: $gray-900;
    }
  
    .fc-view {
      > table {
        background-color: $white;
  
        > {
          thead {
            th, td {
              border-color: $gray-300;
            }
          }
  
          tbody > tr > td {
            border-color: $gray-300;
          }
        }
      }
  
      .fc-other-month {
        background-color: #fcfcfc;
      }
  
      .fc-day-number {
        font-size: 14px;
        font-weight: 400;
        font-family: Arial, sans-serif;
        color: $gray-700;
        display: inline-block;
        padding: 5px 8px;
        position: relative;
        transition: all 0.2s ease-in-out;
        margin: 2px 2px 0 0;
        min-width: 20px;
        text-align: center;
  
        &:hover, &:focus {
          color: $gray-900;
          background-color: $gray-100;
        }
      }
  
      .fc-scroller {
        height: auto !important;
        overflow: visible !important;
      }
  
      &.fc-agenda-view .fc-day-header {
        > a > span {
          display: block;
  
          &:first-child {
            color: $gray-600;
            font-size: 8px;
            font-weight: 400;
            line-height: 1.2;
          }
  
          &:last-child {
            font-weight: 700;
            font-size: 14px;
            font-family: Helvetica, Arial, sans-serif;
            line-height: 1;
          }
        }
  
        &.fc-today > a {
          color: $primary;
        }
      }
  
      &.fc-listMonth-view, &.fc-listWeek-view {
        border-width: 0;
      }
  
      &.fc-listMonth-view .fc-scroller, &.fc-listWeek-view .fc-scroller {
        padding: 0 20px 20px;
      }
  
      &.fc-listMonth-view .fc-list-table, &.fc-listWeek-view .fc-list-table {
        border-collapse: separate;
        display: block;
      }
  
      &.fc-listMonth-view .fc-list-table > tbody, &.fc-listWeek-view .fc-list-table > tbody {
        display: flex;
        flex-wrap: wrap;
      }
  
      &.fc-listMonth-view .fc-list-heading, &.fc-listWeek-view .fc-list-heading {
        flex: 0 0 20%;
        max-width: 20%;
        margin-top: 15px;
      }
  
      &.fc-listMonth-view .fc-list-heading:first-child, &.fc-listWeek-view .fc-list-heading:first-child, &.fc-listMonth-view .fc-list-heading:first-child + .fc-list-item, &.fc-listWeek-view .fc-list-heading:first-child + .fc-list-item {
        margin-top: 0;
      }
  
      &.fc-listMonth-view .fc-list-heading td, &.fc-listWeek-view .fc-list-heading td {
        background-color: transparent;
        border-width: 0;
        padding: 0;
      }
  
      &.fc-listMonth-view .fc-list-heading-main, &.fc-listWeek-view .fc-list-heading-main {
        display: block;
        font-size: 11px;
        text-transform: uppercase;
        color: $gray-600;
        font-weight: 600;
        padding: 5px;
        text-align: center;
      }
  
      &.fc-listMonth-view .fc-list-heading-main span:last-child, &.fc-listWeek-view .fc-list-heading-main span:last-child {
        display: block;
        font-size: 30px;
        font-weight: 700;
        font-family: Helvetica, Arial, sans-serif;
        letter-spacing: -1px;
        color: $gray-900;
        line-height: 1;
      }
  
      &.fc-listMonth-view .fc-list-heading-main.now, &.fc-listWeek-view .fc-list-heading-main.now, &.fc-listMonth-view .fc-list-heading-main.now span:last-child, &.fc-listWeek-view .fc-list-heading-main.now span:last-child {
        color: $primary;
      }
  
      &.fc-listMonth-view .fc-list-item, &.fc-listWeek-view .fc-list-item {
        flex: 0 0 calc(80% - 5px);
        max-width: calc(80% - 5px);
        dispLay: flex;
        flex-direction: column;
        border-left: 4px solid transparent;
        background-color: $white;
        margin-top: 15px;
      }
  
      &.fc-listMonth-view .fc-list-item {
        &:hover, &:focus {
          cursor: pointer;
        }
      }
  
      &.fc-listWeek-view .fc-list-item {
        &:hover, &:focus {
          cursor: pointer;
        }
      }
  
      &.fc-listMonth-view .fc-list-item {
        &:hover td, &:focus td {
          background-color: transparent;
        }
      }
  
      &.fc-listWeek-view .fc-list-item {
        &:hover td, &:focus td {
          background-color: transparent;
        }
      }
  
      &.fc-listMonth-view .fc-list-item > td, &.fc-listWeek-view .fc-list-item > td {
        border-width: 0;
      }
  
      &.fc-listMonth-view .fc-list-item > td.fc-list-item-time, &.fc-listWeek-view .fc-list-item > td.fc-list-item-time {
        border-top: 1px solid $gray-200;
        border-right: 1px solid $gray-200;
        box-sizing: border-box;
      }
  
      &.fc-listMonth-view .fc-list-item > td.fc-list-item-title, &.fc-listWeek-view .fc-list-item > td.fc-list-item-title {
        border-bottom: 1px solid $gray-200;
        border-right: 1px solid $gray-200;
        box-sizing: border-box;
      }
  
      &.fc-listMonth-view .fc-list-item + .fc-list-item, &.fc-listWeek-view .fc-list-item + .fc-list-item {
        margin-left: 20%;
      }
  
      &.fc-listMonth-view .fc-list-item-marker, &.fc-listWeek-view .fc-list-item-marker {
        display: none;
      }
  
      &.fc-listMonth-view .fc-list-item-time, &.fc-listWeek-view .fc-list-item-time {
        padding: 12px 15px 2px;
        font-size: 13px;
        text-transform: capitalize;
        width: 100%;
      }
  
      &.fc-listMonth-view .fc-list-item-title, &.fc-listWeek-view .fc-list-item-title {
        padding: 0 15px 12px;
      }
  
      &.fc-listMonth-view .fc-list-item-title a, &.fc-listWeek-view .fc-list-item-title a {
        display: block;
        font-weight: 700;
        font-size: 15px;
        margin-bottom: 5px;
        color: $gray-900;
      }
  
      &.fc-listMonth-view .fc-list-item-title .fc-desc, &.fc-listWeek-view .fc-list-item-title .fc-desc {
        font-size: 13px;
        line-height: 1.375;
        display: block;
      }
    }
  
    td.fc-today {
      background-color: $primary;
  
      .fc-day-number {
        background-color: $primary;
        color: $white;
        border-radius: 1px;
  
        &:hover, &:focus {
          background-color: $primary;
          color: $white;
          border-radius: 1px;
        }
      }
    }
  
    .fc-axis {
      font-size: 11px;
    }
  
    .fc-event {
      border-width: 0;
      border-top-width: 5px;
      border-radius: 0;
  
      &.fc-day-grid-event > div {
        display: none;
  
        .fc-time {
          font-weight: 500;
        }
  
        .fc-desc {
          display: none;
        }
      }
    }
  
    .fc-time-grid-event {
      padding: 5px;
      border-left-width: 0;
      border-top-width: 2px;
  
      .fc-time {
        font-size: 12px;
      }
  
      .fc-title {
        font-size: 14px;
        font-weight: 700;
        color: $gray-900;
        margin-bottom: 10px;
      }
  
      .fc-desc {
        font-size: 13px;
        line-height: 1.3;
        color: rgba(28, 39, 60, 0.8);
      }
    }
  
    .fc-agendaWeek-view .fc-time-grid-event > div {
      display: none;
  
      .fc-time {
        font-size: 11px;
      }
  
      .fc-title {
        font-size: 13px;
      }
  
      .fc-desc {
        display: none;
      }
    }
  
    .fc-list-empty {
      padding: 20px 0;
      background-color: $white;
      border: 1px solid $gray-300;
    }
  }
  
